
.gallery-container {
    width: 100%;
    font-family: 'Microsoft YaHei', 'PingFang SC', sans-serif;
    background: linear-gradient(180deg, var(--theme-bg-main) 0%, var(--theme-bg-secondary) 100%);
    border-radius: 12px;
    overflow: hidden;
    color: var(--theme-text-primary);
    position: relative;

    // ========================================================================
    // üñºÔ∏è ÂèëÂÖâËæπÊ°ÜÊïàÊûú
    // ========================================================================
    &.frame-glow {
        box-shadow:
            0 0 var(--frame-glow-size, 15px) var(--frame-glow-color, #4488ff),
            inset 0 0 calc(var(--frame-glow-size, 15px) / 2) rgba(255, 255, 255, 0.1);
        border: 2px solid var(--frame-glow-color, #4488ff);

        &.frame-animated {
            animation: glowPulse 3s ease-in-out infinite;
        }
    }

    // ========================================================================
    // üñºÔ∏è ÂõæÊ°àËæπÊ°ÜÊïàÊûú - ‰ΩøÁî® CSS Ê∏êÂèòÊ®°ÊãüËä±Á∫π
    // ========================================================================
    &.frame-pattern {
        border: 3px solid transparent;
        background-clip: padding-box;
        position: relative;

        &::before {
            content: '';
            position: absolute;
            top: -3px;
            left: -3px;
            right: -3px;
            bottom: -3px;
            border-radius: 14px;
            background: repeating-linear-gradient(45deg,
                    var(--frame-pattern-color, #ffaabb) 0px,
                    var(--frame-pattern-color, #ffaabb) 2px,
                    transparent 2px,
                    transparent 8px),
                repeating-linear-gradient(-45deg,
                    var(--frame-pattern-color, #ffaabb) 0px,
                    var(--frame-pattern-color, #ffaabb) 2px,
                    transparent 2px,
                    transparent 8px);
            opacity: 0.6;
            z-index: -1;
        }

        &::after {
            content: '';
            position: absolute;
            top: 0;
            left: 0;
            right: 0;
            bottom: 0;
            border-radius: 12px;
            border: 2px solid var(--frame-pattern-color, #ffaabb);
            opacity: 0.8;
            pointer-events: none;
        }
    }

    // ========================================================================
    // üñºÔ∏è ÂõæÁâáËæπÊ°ÜÊïàÊûú
    // ========================================================================
    &.frame-image {
        border: var(--frame-border-width, 20px) solid transparent;
        border-image: var(--frame-border-image) var(--frame-border-slice, 30) fill;
        border-image-repeat: stretch;
        border-radius: 0; // border-image ‰∏çÊîØÊåÅÂúÜËßí
    }

    // ========================================================================
    // üñºÔ∏è ÂõõËßíË£ÖÈ•∞ÊïàÊûú
    // ========================================================================
    &.frame-corner {

        &::before,
        &::after {
            content: '';
            position: absolute;
            width: var(--frame-corner-size, 40px);
            height: var(--frame-corner-size, 40px);
            background-image: var(--frame-corner-image);
            background-size: contain;
            background-repeat: no-repeat;
            z-index: 10;
            pointer-events: none;
        }

        &::before {
            top: -5px;
            left: -5px;
        }

        &::after {
            top: -5px;
            right: -5px;
            transform: scaleX(-1);
        }

        // ‰ΩøÁî®È¢ùÂ§ñÂÖÉÁ¥†ÂÆûÁé∞Â∫ïÈÉ®‰∏§‰∏™ËßíËêΩ
        .corner-bottom-left,
        .corner-bottom-right {
            position: absolute;
            width: var(--frame-corner-size, 40px);
            height: var(--frame-corner-size, 40px);
            background-image: var(--frame-corner-image);
            background-size: contain;
            background-repeat: no-repeat;
            z-index: 10;
            pointer-events: none;
        }

        .corner-bottom-left {
            bottom: -5px;
            left: -5px;
            transform: scaleY(-1);
        }

        .corner-bottom-right {
            bottom: -5px;
            right: -5px;
            transform: scale(-1, -1);
        }
    }
}

// ÂèëÂÖâÂëºÂê∏Âä®Áîª
@keyframes glowPulse {

    0%,
    100% {
        box-shadow:
            0 0 var(--frame-glow-size, 15px) var(--frame-glow-color, #4488ff),
            inset 0 0 calc(var(--frame-glow-size, 15px) / 2) rgba(255, 255, 255, 0.1);
    }

    50% {
        box-shadow:
            0 0 calc(var(--frame-glow-size, 15px) * 1.5) var(--frame-glow-color, #4488ff),
            0 0 calc(var(--frame-glow-size, 15px) * 2) var(--frame-glow-color, #4488ff),
            inset 0 0 var(--frame-glow-size, 15px) rgba(255, 255, 255, 0.15);
    }
}

// Ê†áÈ¢òÊ†è
.gallery-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 16px 20px;
    background: var(--theme-bg-header);
    border-bottom: 1px solid var(--theme-border-color);

    .header-title {
        display: flex;
        align-items: center;
        gap: 10px;
        font-size: 18px;
        font-weight: bold;
        color: var(--theme-text-primary);

        i {
            color: var(--theme-primary);
        }
    }

    .header-info {
        display: flex;
        align-items: center;
        gap: 16px;
        font-size: 13px;
        color: var(--theme-text-secondary);

        .current-char {
            display: flex;
            align-items: center;
            gap: 6px;
            padding: 4px 10px;
            background: var(--theme-primary-light);
            border-radius: 12px;

            i {
                color: var(--theme-primary);
            }
        }
    }
}

// ËßíËâ≤Á≠õÈÄâ
.character-filter {
    display: flex;
    flex-wrap: wrap;
    gap: 8px;
    padding: 12px 20px;
    background: rgba(25, 30, 50, 0.5);
    border-bottom: 1px solid var(--theme-border-color);

    .filter-btn {
        padding: 6px 14px;
        background: rgba(50, 60, 80, 0.5);
        border: 1px solid var(--theme-border-color);
        border-radius: 16px;
        color: var(--theme-text-secondary);
        font-size: 12px;
        cursor: pointer;
        transition: all 0.2s;

        &:hover {
            background: var(--theme-primary-dark);
            color: var(--theme-text-primary);
        }

        &.active {
            background: var(--theme-primary-light);
            border-color: var(--theme-primary);
            color: var(--theme-text-primary);
        }
    }
}

// ÂõæÁâáÁΩëÊ†º - Ê∞¥Âπ≥ÊªöÂä®Â∏ÉÂ±Ä
.gallery-grid {
    display: flex;
    flex-wrap: nowrap;
    justify-content: center; // ÂΩìÂç°ÁâáÂ∞ëÊó∂Â±Ö‰∏≠ÊòæÁ§∫
    gap: 20px;
    padding: 20px;
    min-height: 200px;
    overflow-x: auto;
    overflow-y: hidden;
    scroll-behavior: smooth;
    -webkit-overflow-scrolling: touch;

    // Ëá™ÂÆö‰πâÊªöÂä®Êù°Ê†∑Âºè
    &::-webkit-scrollbar {
        height: 8px;
    }

    &::-webkit-scrollbar-track {
        background: rgba(30, 40, 60, 0.5);
        border-radius: 4px;
    }

    &::-webkit-scrollbar-thumb {
        background: rgba(100, 150, 200, 0.4);
        border-radius: 4px;

        &:hover {
            background: rgba(100, 150, 200, 0.6);
        }
    }
}

// ÁøªËΩ¨Âç°Áâá
// ============================================================================
// üé® Âç°ÁâáÂ∞∫ÂØ∏ÈÖçÁΩÆ - ‰øÆÊîπËøôÈáåÁöÑÂÄºÂèØ‰ª•Ë∞ÉÊï¥Âç°ÁâáÂ§ßÂ∞è
// ============================================================================
.flip-card {
    flex-shrink: 0; // Èò≤Ê≠¢Âç°ÁâáË¢´ÂéãÁº©
    width: 320px; // Âõ∫ÂÆöÂç°ÁâáÂÆΩÂ∫¶
    aspect-ratio: 2 / 3; // ‰ΩøÁî®ÂÆΩÈ´òÊØî‰ª£ÊõøÂõ∫ÂÆöÈ´òÂ∫¶Ôºå‰øùÊåÅ 2:3 ÊØî‰æã
    perspective: 1000px;
    cursor: pointer;

    &:hover .flip-card-inner {
        box-shadow: 0 15px 40px var(--theme-card-hover-shadow);
        transform: scale(1.03);
    }
}

.flip-card-inner {
    position: relative;
    width: 100%;
    height: 100%;
    transition: transform 0.7s cubic-bezier(0.4, 0, 0.2, 1), box-shadow 0.3s;
    transform-style: preserve-3d;
    border-radius: 14px;
    box-shadow: 0 8px 25px var(--theme-card-shadow);
}

.flip-card.flipped .flip-card-inner {
    transform: rotateY(180deg);
}

.flip-card-front,
.flip-card-back {
    position: absolute;
    width: 100%;
    height: 100%;
    backface-visibility: hidden;
    border-radius: 14px;
    overflow: hidden;
    border: 2px solid var(--theme-card-border);

    img {
        width: 100%;
        height: 100%;
        object-fit: cover;
        transition: transform 0.3s;
    }

    &:hover img {
        transform: scale(1.05);
    }
}

.flip-card-front {
    .card-overlay {
        position: absolute;
        bottom: 0;
        left: 0;
        right: 0;
        padding: 16px;
        background: linear-gradient(to top, rgba(0, 0, 0, 0.9) 0%, rgba(0, 0, 0, 0.5) 50%, transparent 100%);
    }

    .card-name {
        font-size: 16px;
        font-weight: bold;
        color: #fff;
        text-shadow: 0 2px 4px rgba(0, 0, 0, 0.8);
        margin-bottom: 4px;
    }

    .card-character {
        font-size: 12px;
        color: rgba(255, 255, 255, 0.7);
    }
}

.flip-card-back {
    transform: rotateY(180deg);
}

// Á©∫Áä∂ÊÄÅ
.empty-state {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 60px 20px;
    color: var(--theme-text-muted);

    i {
        font-size: 48px;
        margin-bottom: 16px;
        opacity: 0.5;
    }

    p {
        margin: 4px 0;
        font-size: 14px;
    }

    .hint {
        font-size: 12px;
        color: var(--theme-text-muted);
    }
}

// Â∫ïÈÉ®ÊèêÁ§∫
.gallery-footer {
    text-align: center;
    padding: 12px 20px;
    font-size: 12px;
    color: var(--theme-text-muted);
    background: var(--theme-bg-main);
    border-top: 1px solid var(--theme-border-color);
}
